h1{
  font-family: 'Cinzel';
  color:  #117470;
}

body{
  background-image: -webkit-gradient(linear, right top, left bottom, from(#050e1c), color-stop(#0a2636), color-stop(#003f4f), color-stop(#005963), to(#117470));
  background-image: -webkit-linear-gradient(right top, #050e1c, #0a2636, #003f4f, #005963, #117470);
  background-image: -o-linear-gradient(right top, #050e1c, #0a2636, #003f4f, #005963, #117470);
  background-image: linear-gradient(to left bottom, #050e1c, #0a2636, #003f4f, #005963, #117470);
}

.container{
  padding-top: 5px;
  padding-left: 163px;
  padding-right: 155px;
}

#header{
  text-align: center;
}

#score{
  text-align: center;
}

h3{
  text-align: center;
  color:  #117470;
  font-family: 'Cinzel';
}

img{
  margin-right: 20px;
  margin-bottom: 20px;
  display: grid;
  float: left;
  text-align: center;
}




